.friend-list {
  width: 300px;
  margin: 0 auto 50px;
  padding-left: 0;
  text-align: center;
  list-style: none;
}

.friend-list_item {
  display: flex;
  align-items: center;
  padding: 15px;
  box-shadow: 2px 3px 2px rgb(0 0 0 / 10%), 0px 2px 2px rgb(0 0 0 / 10%),
    0px 2px 2px rgb(0 0 0 / 12%), 0px 3px 2px rgb(0 0 0 / 10%);
  border-top: 1px solid rgb(243, 242, 242);
}

.friend-list_item:not(:last-child) {
  margin-bottom: 15px;
}

.friend-list_status {
  width: 50px;
  margin-right: 10px;
  font-size: 10px;
  background-color: tomato;
  color: tomato;
  border-radius: 50%;
}

.friend-list_avatar {
  width: 100px;
  margin-right: 20px;
}

.friend-list_name {
  font-weight: 600;
  font-size: 25px;
}
